// VARIABLES //
@import "../../../../../../mainSCSS/_variables";
// MIXINS //
@import "../../../../../../mainSCSS/_mixins";

.warningText {
  text-align: center;
  color: red;
  margin: 0px 20px !important;
}

.buttonVertragsFormAssistent {
  width: 60% !important;
  height: 45px !important;
  margin: auto !important;
  color: white !important;
  text-transform: none !important;
  border-radius: 22.5px !important;
}

.panel-ausfullen-details {
  display: flex;
  flex-direction: column;
  margin: 15px;

  .panel-ausfullen-form-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .MuiFormControl-root {

      .MuiInputBase-root {
        height: 50px;
        background-color: $lilaBackgroundHell !important;

        input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
          -webkit-box-shadow: $lilaBackgroundHell !important;

        }

        .MuiOutlinedInput-input {
          padding: 15px 14px;

        }
      }
    }
  }
}

.MuiOutlinedInput-root {
  border-radius: 28px !important;
}
