@import "../../mainSCSS/mixins";
@import "../../mainSCSS/variables";

#mainKG {
  @include main;
  right: 0px;
  .kuendigung-header {
    height: 70px;
    width: 100%;
    padding: 20px;
    font-size: 25px;
    box-sizing: border-box;
    border-bottom: $border;
    p {
      margin: 0px auto;
      color: $textColorGrey;
    }
  }
  .kuendigung-form {
    display: flex;
    flex-direction: column;
    margin: 80px auto;
    max-width: 600px;
    width: auto;
    border: $border;
    height: auto;
    box-sizing: border-box;
    padding: 20px;
    -moz-box-shadow: 3px 3px 3px 3px #ccc;
    -webkit-box-shadow: 3px 3px 3px 3px #ccc;
    box-shadow: 3px 3px 4px 5px #ccc;
    transition-duration: 0.5s;
    .kuendigung-form-row-right {
      font-size: 12px;
      margin-bottom: 10px;
      text-align: right;
      .kuendigung-form-field-right {
        margin-top: 20px;
        .MuiFormControl-root {
          font-size: 12px !important;
          max-width: 150px;
          .MuiIconButton-root {
            padding: 0px !important;
          }
          .MuiInputLabel-outlined {
            transform: translate(10px, 8px) scale(1);
            font-size: 12px;
          }
          .MuiInputLabel-outlined.MuiInputLabel-shrink {
            transform: translate(14px, -6px) scale(0.75);
            font-size: 13px;
          }
          .MuiInputBase-input {
            font-size: 12px;
          }
        }
        label {
          margin-right: 10px;
        }
        input {
          height: 28px;
          width: 150px;
          color: $textColorGrey;
          box-sizing: border-box;
          padding: 5px;
        }
      }
    }
    .kuendigung-form-row-left {
      font-size: 12px;
      text-align: left;
      .inputFieldLang {
        max-width: 315px;
        .MuiFormControl-root {
          font-size: 12px !important;
          margin-right: 15px;
          width: 100% !important;
          .MuiInputLabel-outlined {
            transform: translate(10px, 8px) scale(1);
            font-size: 12px;
          }
          .MuiInputLabel-outlined.MuiInputLabel-shrink {
            transform: translate(14px, -6px) scale(0.75);
            font-size: 13px;
          }
          .MuiInputBase-input {
            font-size: 12px;
            padding: 6px 0px 7px 5px;
            color: $textColorGrey;
          }
        }
      }
      .kuendigung-form-field-left {
        .MuiFormControl-root {
          font-size: 12px !important;
          max-width: 150px;
          margin-right: 15px;
          .MuiInputLabel-outlined {
            transform: translate(10px, 8px) scale(1);
            font-size: 12px;
          }
          .MuiInputLabel-outlined.MuiInputLabel-shrink {
            transform: translate(14px, -6px) scale(0.75);
            font-size: 13px;
          }
          .MuiInputBase-input {
            font-size: 12px;
          }
        }
        input {
          height: 28px;
          width: 150px;
          color: $textColorGrey;
          margin-right: 15px;
          box-sizing: border-box;
          padding: 5px;
          &::placeholder {
            color: $textColorGrey;
          }
        }
        .adresse {
          width: 315px;
        }
        .kuendigung-titel {
          width: 315px;
          margin: 10px 0px 10px 0px;
        }
        .kuendigung-text {
          width: 100%;
          color: $textColorGrey;
          font-family: Arial, Helvetica, sans-serif;
        }
      }
      .popup-overlay {
        overflow-y: auto;
        padding: 50px;
        box-sizing: border-box;
      }
      .popup-content {
        max-width: 700px !important;
        width: 100% !important;
      }
      .signatureCanvas {
        border: $border;
        max-width: 100%;
        height: 400px;
        overflow-y: auto;
        width: 700px !important;
      }
      .signatureImage {
        display: block;
        margin: 0 auto 0 0;
        border: $border;
        width: 250px;
      }
    }
  }
}
.submit-kuendigung {
  margin: 50px auto !important;
  width: 200px;
  height: 50px;
}

.MuiPaper-root {
  color: $textColorGrey !important;
  background-color: white;
  .MuiList-root {
    padding: 0px !important;
    .MuiButtonBase-root {
      font-size: 14px !important;
    }
  }
}

@include MaxMediaQ(700px) {
  #mainKG {
    left: 0px;
    top: 70px;
  }
}
