// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";


.slick-slider {
    position: absolute;
    width: 100%;
    height: 100%;

    .slick-arrow {
        &:before {
            color: $lila;
        }
    }
}

.sliderContener {
    display: flex;
    justify-content: space-around;
    flex-direction: column;

    p {
        display: flex;
        padding: 10px 0px 0px 0px;
        box-sizing: border-box;
        font-weight: bold;
        margin: auto;
        border-bottom: 2px solid;
        width: 80%;
        justify-content: center;
    }

    .container {
        display: flex;
        flex-direction: row;
        padding: 0px;
        margin-top: 0px !important;

        .ring {
            min-height: 170px !important;
            min-width: 170px !important;
            background: white;
            border-radius: 50%;
            display: flex;
            margin: auto 15px;

            .ring2 {
                width: 160px;
                height: 160px;
                background: white;
                border-radius: 50%;
                position: relative;
                display: flex;
                flex-direction: column;
                overflow: auto;
                justify-content: space-between;
                margin: auto;

                .ring-element {
                    color: white;
                    justify-content: center;
                    display: flex;
                    font-size: 11px;

                    span {
                        margin: auto;
                    }
                }
            }
        }

        .contentSlider {
            height: 150px;
            width: 250px;
            background-color: white;
            background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
            display: flex;
            margin: 25px;
            flex-direction: column;

            .contentSlider-element {
                color: $textColorGrey;
                display: flex;
                font-size: 11px;
                float: left;

                span {
                    margin: auto auto auto 0;
                }
            }
        }
    }
}

@include MaxMediaQ(420px) {
    .container {
        height: auto !important;
        flex-direction: column !important;
        align-items: center !important;

        .contentSlider {
            overflow-y: auto;

            .contentSlider-element {
                height: 15px !important;
            }
        }
    }
}
