// MIXINS //

@mixin flexColumn {
  display: flex;
  flex-direction: column;
}
@mixin formFields {
  @include flexColumn;
  font-size: 18px;
  margin-top: 20px;
  label {
    margin-top: 20px;
    width: 100% !important;
  }
  select {
    padding: 15px 35px 15px 15px;
    border: $border;
    font-size: 16px;
    width: 100% !important;
    margin-top: 10px;
  }
}
@mixin button {
  border: 1px solid #4169e1ab;
  border-radius: 5px;
  width: 50%;
  padding: 12px;
  cursor: pointer;
  color: $textColorBlue;
  margin: auto;
  margin-top: 50px;
  &:hover {
    border-color: #4169e14d;
  }
}
@mixin main {
  position: absolute !important;
  top: 0px;
  left: 195px;
  bottom: 0px;
  background: #ffffff;
  overflow-y: auto;
  padding: 0px;
  margin: 0px;
  height: 100%;
}
@mixin vertragspanel {
  position: fixed;
  right: 0px;
  width: 320px;
  z-index: 2;
  height: 100%;
  border-left: $border;
  background: $backgroundGrey;
  box-sizing: border-box;
}
@mixin fensterErfolg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 75px;
  background-color: #7ec219;
  box-sizing: border-box;
  padding: 28px;
  font-size: 19px;
  color: #ffffff;
}
@mixin rowCenter {
  height: 118px;
  margin: 10px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: row;
}
@mixin infoHeader {
  top: 0px;
  position: absolute;
  z-index: 2000;
  height: 50px;
  background-color: rgb(120, 199, 3);
  left: 195px;
  right: 0px;
  color: white;
  font-size: 17px;
  box-sizing: border-box;
  padding: 17px 0 0 40px;
}
@mixin MaxMediaQ($number) {
  @media screen and (max-width: $number) {
    @content;
  }
}
@mixin MinMediaQ($number) {
  @media screen and (min-width: $number) {
    @content;
  }
}

// ANIMATION
@mixin keyframes($animation-name) {
  @-webkit-keyframes #{$animation-name} {
    @content;
  }
  @-moz-keyframes #{$animation-name} {
    @content;
  }
  @-ms-keyframes #{$animation-name} {
    @content;
  }
  @-o-keyframes #{$animation-name} {
    @content;
  }
  @keyframes #{$animation-name} {
    @content;
  }
}

@mixin animation($str) {
  -webkit-animation: #{$str};
  -moz-animation: #{$str};
  -ms-animation: #{$str};
  -o-animation: #{$str};
  animation: #{$str};
}

@include keyframes(arrow) {
  0% {
    margin-top: 15px;
  }
  100% {
    margin-bottom: 15px;
  }
}

@include keyframes(arrow2) {
  0% {
    margin-bottom: 15px;
  }
  100% {
    margin-top: 15px;
  }
}
