@import "../../mainSCSS/mixins";
@import "../../mainSCSS/variables";

.loadingContainer {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  flex-direction: column;
  .loadingImageContainer {
    animation: rotate 20s infinite;
    .loadingImage {
      width: 200px;
      height: 200px;
      transform: scale(1);
      animation: pulse 2s infinite;
    }
  }
  p {
    font-size: 25px;
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
  }

  70% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(0.95);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
