@import "../../../mainSCSS/mixins";
@import "../../../mainSCSS/variables";

#vertragspanel,
#vertragspanelIsOpen {
  @include vertragspanel;
  overflow-y: auto;
  top: 0px;
  background-color: white;
  background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
  @include flexColumn;

  .vertragspanel-titel {
    margin: 10px;
    padding: 10px 0 10px 0;
    border-bottom: 2px solid transparent !important;
    border-left-width: 0 !important;
    border-top-width: 0 !important;
    border-right-width: 0 !important;
    border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
    border-image-slice: 1 !important;
    box-sizing: border-box;
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    h1 {
      box-sizing: border-box;
      font-size: 22px;
      text-align: center;
      line-height: 35px;
      margin: 0px;
      top: 30px;
      padding-bottom: 20px;
      color: $lila;
    }

    p {
      font-size: 16px;
      text-align: center;
      color: black;
    }
  }

  #vertragspanel-vertragslist {
    padding: 0px;
    margin: 10px;
    text-align: center;
    border-bottom: 2px solid transparent !important;
    border-left-width: 0 !important;
    border-top-width: 0 !important;
    border-right-width: 0 !important;
    border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
    border-image-slice: 1 !important;

    p {
      color: black;
    }

    .vertragslist-element {
      list-style: none;
      text-align: center;
      margin: 10px 30px 10px 30px;
      border: $border;
      background: #ffffff;
      height: 50px;
      border-radius: 10px;
      display: flex;
      flex-direction: row;
      position: relative;

      .vertragslist-element-text {
        text-align: left;
        margin: 8px 0 2px 5px;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 130px;
        color: black;
      }

      .vertragslist-element-image {
        margin: 5px;
        width: 40px;
        height: 40px;
        border: 2px solid $lila;
        border-radius: 50%;
        font-size: 25px;
        display: flex;

        img {
          font-size: 8px;
          max-height: 30px;
          max-width: 30px;
          margin: auto;
        }
      }

      .fa-times {
        position: absolute;
        font-size: 15px;
        cursor: pointer;
        right: 10px;
        top: 10px;
        color: $lila;

        &:hover {
          font-size: 17px;
          right: 9px;
          top: 9px;
        }
      }
    }
  }
   a {
      position: relative;
      display: flex;
      margin: auto;
      height: 42px;
      width: 150px;
      bottom: 0px;

      .vertragspanel-button {
        @include button;
        margin: auto auto 50px auto;
        width: 100%;
        background-color: $greenColor;
        color: white;
        border: none;
        border-radius: 15px;

        &:hover {
          background-color: darken($color: $greenColor, $amount: 5);
        }
    }
  }
}

@include MaxMediaQ(900px) {
  #vertragspanelIsOpen {
    @include vertragspanel;
    left: 195px;
    bottom: 0px;
    width: auto;
    height: 100%;
    top: auto;
    overflow-y: auto;
    z-index: 100;
    background-color: white;
    background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
    display: none;

    .vertragspanel-titel {
      margin: 0px;
      height: 150px;

      h1 {
        padding-bottom: 0px;
      }

      p {
        margin: 0px;
      }

      div {
        color: $lila;
        cursor: pointer;
        text-align: center;
        font-size: 22px;
        transform: rotate(180deg);
        @include animation("arrow2 normal 1.5s linear 0s 500000 both");
      }
    }
  }

  #vertragspanel {
    @include vertragspanel;
    left: 195px;
    bottom: 0px;
    width: auto;
    height: 150px;
    top: auto;
    overflow-y: hidden;
    background-color: white;
    background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
    display: none;
    button {
      display: none;
    }

    .vertragspanel-titel {
      border-bottom: none !important;
      display: flex !important;
      flex-direction: column-reverse !important;
      margin-top: 0px !important;

      h1 {
        padding-bottom: 0px !important;
        border-top: 1px solid transparent !important;
        border-left-width: 0 !important;
        border-bottom-width: 0 !important;
        border-right-width: 0 !important;
        border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
        border-image-slice: 1 !important;
      }

      div {
        color: $lila;
        cursor: pointer;
        text-align: center;
        font-size: 22px;
        height: 26px;
        @include animation("arrow normal 1.5s linear 0s 500000 both");
      }

      p {
        margin: 5px;
      }
    }
  }
}

@include MaxMediaQ(700px) {
  #vertragspanel {
    @include vertragspanel;
    left: 0px !important;
    bottom: 60px !important;
    width: auto !important;
    height: 90px !important;
    background-color: white !important;
    background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight) !important;

    button {
      display: none;
    }

    .vertragspanel-titel {
      p {
        font-size: 14px;
        margin: 0px;
        color: black;
      }

      h1 {
        display: none;
      }
    }
  }

  #vertragspanelIsOpen {
    top: 60px;
    height: auto;
    left: 0px;
    bottom: 70px;
    overflow-y: auto;
    background-color: white;
  }
}
