// VARIABLES //
@import "../../mainSCSS/_variables";
// MIXINS //
@import "../../mainSCSS/_mixins";

#mainVA {
  @include main;
  right: 320px;
}

#mainVA::-webkit-scrollbar {
  width: 12px;
  background-color: lighten($color: $lila, $amount: 40);
}

#mainVA::-webkit-scrollbar-thumb {
  background-color: lighten($color: $lila, $amount: 20);
  border-radius: 6px;
  border: 3px solid lighten($color: $lila, $amount: 20);
}

.mainVA-header {
  display: none;
}

@include MaxMediaQ(900px) {
  #mainVA {
    right: 0px;
/*     bottom: 150px;
 */    height: auto;
  }
}

@include MaxMediaQ(700px) {
  #mainVA {
    left: 0px;
/*     bottom: 150px;
 */    top: 60px;
 bottom: 70px;
  }

  .mainVA-header {
    z-index: 200;
    display: flex;
    position: absolute;
    height: 60px;
    width: 100%;
    top: 0px;
    background-color: white;
    background-image: $backgroundLinear;
    border-bottom-right-radius: 15px;
    justify-content: center;

    p {
      color: rgba(255, 255, 255, 0.883);
      font-size: 20px;
      letter-spacing: 1.5px;
      font-weight: bold;

    }
  }

  .after {
    position: absolute;
    top: 60px;
    height: 20px;
    width: 13px;
    background-color: #7261f3;
    z-index: 200;
    border-bottom-right-radius: 19px;

    .inside-after {
      height: 100%;
      width: 100%;
      background-color: white;
      border-top-left-radius: 20px;
    }
  }
}
