// VARIABLES //
@import "../../../../../../mainSCSS/_variables";
// MIXINS //
@import "../../../../../../mainSCSS/_mixins";

.cardAssistentContainer {
  width: 400px;
  height: 100%;
  position: relative;
  background-color: white;
  border-radius: 25px;

  .cardAssistentHeader {
    display: flex;
    justify-content: center;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    height: 180px;
    background: white;
    background-color: $lilaBackgroundHell;
    align-content: center;
    margin-bottom: 40px;
    #buttonCloseDialog1{
      color: $lila;
      position: absolute;
      right: 15px;
      top: 15px;
    }
    .back {
      position: absolute;
      left: 20px;
      top: 20px;
      color: $lila;
      height: 30px;
      width: 20px;
      font-size: 25px;
      cursor: pointer;
    }

    .image-container {
      height: 140px;
      width: 140px;
      border-radius: 50%;
      background-color: white;
      display: flex;
      margin: auto 20px;

      img {
        background-color: white;
        display: flex;
        max-height: 100px;
        max-width: 100px;
        margin: auto;
        cursor: pointer;
      }
    }

    .info-container {
      display: flex;
      justify-content: center;
      flex-direction: column;

      p {
        margin-bottom: 0px;
        color: black;

        &:nth-child(2n + 1) {
          font-weight: bold;
        }
      }
    }
  }

  .cardAssistentBody {
    height: 100%;

    .title {
      box-sizing: border-box;
      padding: 15px;
      text-align: center;
      background-color: $backgroundGrey;
      margin-bottom: 40px;

      h3 {
        font-size: 17px;
        font-weight: 100;
        margin: 0px;
      }
    }
  }
}
