// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";


.loginWrapper {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: white;
  background-image: url("../../../assets/images/1304898.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &:after {
    content: "";
    background-image: linear-gradient(to left, $lila, rgba(114, 59, 202, 0.76));
    background-color: white;
    width: 100%;
    height: 100%;
    opacity: 0.8;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: fixed;
  }

  position: absolute;
  top: 0px;
  overflow-x: hidden;
  padding: 20px;
  box-sizing: border-box;

  .loginContainer {
    display: flex;
    margin: 50px auto;
    flex-direction: column;
    border: $border;
    box-sizing: border-box;
    padding: 30px;
    border-radius: 15px;
    background-color: white;
    width: 362px;
    z-index: 10;

    .logo1 {
      width: 300px;
      height: 300px;
      filter: brightness(0) invert(1) opacity(25%);
      z-index: 0;
      position: absolute;
      right: -140px;
      top: 0px;
      background-size: cover;
      transform: rotate(90deg);
      display: none;
    }

    .logo2 {
      width: 300px;
      height: 300px;
      filter: brightness(0) invert(1) opacity(25%);
      z-index: 0;
      position: absolute;
      left: -140px;
      bottom: 0px;
      background-size: cover;
      transform: rotate(180deg);
      display: none;
    }

    .loginContentContainer {
      .my-text-field {
        width: 300px;
        display: flex;
        flex-direction: column;
        text-align: center;

        p {
          color: $lila;
          font-weight: 600;
          font-size: 24px;
          margin-top: 0;
          font-family: 'Lobster', cursive;
        }

        .logo {
          display: flex;
          margin: auto;
          margin-bottom: 20px;
          width: 100%;
          height: 120px;
          background-size: cover;
        }
      }

      .button {
        display: flex;
        margin: auto;
        margin-bottom: 20px;
        text-transform: none;
        background-color: $lila;
        color: white;
        border-radius: 20px;
        width: 80%;

        &:hover {
          background-color: darken($color: $lila, $amount: 10);
        }
      }

      a {
        z-index: 10;
        display: flex;
        justify-content: center;

        .text {
          text-align: center;
          color: $lila;
          margin: 0;
          box-sizing: border-box;
          padding: 5px;
          cursor: pointer;
          font-size: 14px;
          font-weight: 600;


          &:hover {
            color: darken($color: $lila, $amount: 10);
          }
        }
      }
    }
  }
}

form {
  .MuiFormControl-root {
    .MuiInputBase-root {

      input:-webkit-autofill,
      input:-webkit-autofill:hover,
      input:-webkit-autofill:focus,
      input:-webkit-autofill:active {
        -webkit-box-shadow: 0 0 0 30px white inset !important;
      }
    }
  }
}



.MuiOutlinedInput-notchedOutline {
  border-color: $lila !important;
}

@include MaxMediaQ(500px) {
  .loginWrapper {
    justify-content: center;
  }

  form {
    .MuiFormControl-root {
      .MuiInputBase-root {

        input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
          -webkit-box-shadow: 0 0 0 30px rgba(191, 99, 253, 0.356) inset !important;
        }
      }
    }
  }
}

@include MaxMediaQ(500px) {
  .loginWrapper {
    background-image: linear-gradient(to left, $lila, rgba(114, 59, 202, 0.76));
    justify-content: center;

    .loginContainer {
      background-color: rgba(245, 243, 243, 0.685);

      .logo1,
      .logo2 {
        display: block;
      }
    }
  }
}

@include MaxMediaQ(362px) {
  .loginContainer {
    width: 100% !important;
  }

  .loginWrapper {
    padding: 20px 0px;
  }
}
