@import '../../../../mainSCSS/variables';

.top-nav {
  display: none;
  flex-direction: row;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 70px;
  /* background: #f6f6f6; */
  background: #ffffff;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  border-top: 1px solid lighten($color: $lila, $amount: 25);
  width: 100%;
  z-index: 1;
  border-bottom: $border;
  z-index: 100000000000000000;
  box-shadow: 10px 10px 15px 20px lighten($color: $lila, $amount: 30) !important;

  .button,
  .icon,
  .top-nav-post,
  .top-nav-add,
  .top-nav-profil {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $textColorGrey;
    cursor: pointer;
    font-size: 150%;
    height: 80%;
    width: 20%;
    margin: auto;

    .button-box,
    .button-box-home {
      height: 50px;
      width: 50px;
      background: lighten($color: $lila, $amount: 10);
      border-radius: 50%;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.5rem;

      &:hover {
        font-size: 1.7rem;

      }

      a {
        color: white !important;
        text-decoration: none;
      }
    }

    .button-box-home {
      height: 60px;
      width: 60px;
      display: flex;
      justify-content: center;
      align-items: center;

      #vertragsengel-logo1 {
        height: 50px;
        filter: brightness(0) invert(1) opacity(100%);
        display: flex;
        margin: auto;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .top-nav {
    display: flex !important;
  }
}

@media screen and (min-width: 701px) {
  #nav {
    display: none;
  }
}
