// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";

.row-info-window {
  margin: 30px 20px 30px 20px;
  font-family: Arial, Helvetica, Verdana, sans-serif;
  display: flex;
  justify-content: center;

  .row-center {
    width: 1100px;
    height: 142px;
    position: relative;
    display: flex;
    box-sizing: border-box;
    /* border-top: $borderLila;
    border-bottom: $borderLila; */
    color: $lila;

    .row-center-1 {
      width: 33.3%;
      height: 140px;
      position: absolute;
      left: 0;
      right: 66.6%;
      /* border-left: $border;
      border-right: $border; */

      .row-center-content-1 {
        @include rowCenter;
        justify-content: center;

        .avatar-container {
          border-radius: 50%;
          background-image: url(../../../assets//images/avatar.jpg);
          width: 100px;
          height: 100px;
          border: 2px solid $lila;
          left: 30px;
          margin: 5px 5px 0 0;
        }

        .avatar-info-container {
          font-size: 12px;
          width: 180px;
          right: 0px;
          height: 100%;
          display: flex;
          flex-direction: column;

          .buttonInfoPanel {
            text-transform: none;
            width: 170px;
            margin-top: 20px;
            border-radius: 18px;
          }

          .avatar-info-container>p {
            margin: 5px 0 0 0;
          }

          #kontakt-aufnehmen-button {
            height: 20px;
            position: absolute;
            bottom: 0px;
            background: #4169e1b3;
            padding: 5px 15px 5px 15px;
            border-radius: 5px;
            color: #ffffff;
            cursor: pointer;

            &:hover {
              background: lighten($color: #4169e1b3, $amount: 15%);
            }

            p {
              margin: 0;
              margin-top: 4px;
              color: white;
            }
          }
        }
      }
    }

    .two-row-center {
      position: absolute;
      left: 33.3%;
      width: 66.7%;
      display: flex;
      flex-direction: row;

      .row-center-2 {
        position: relative;
        left: 2px;
        width: 50%;
        height: 140px;
        text-align: center;
        display: flex;
        flex-direction: column;
        font-family: serif;


        .row-center-content-2 {
          @include rowCenter;
          flex-direction: column;
          box-sizing: border-box;
          margin: 20px 40px;
          border-radius: 15px;
          border: $borderLila;

          .gesamtkosten {
            font-size: 50px;
            width: 100%;
            display: flex;
            justify-content: center;

            .gesamtkostenAfter {
              font-size: 30px;
              margin: 15px 0 0 5px;
            }
          }

          .gesamtkosten-text {
            font-size: 14px;
          }
        }
      }

      .row-center-3 {
        position: relative;
        left: 2px;
        width: 50%;
        height: 140px;
        text-align: center;
        font-family: serif;
        display: flex;
        flex-direction: column;


        .row-center-content-3 {
          @include rowCenter;
          flex-direction: column;
          box-sizing: border-box;
          margin: 20px 40px;
          border-radius: 15px;
          border: $borderLila;

          .laufende-vertrage {
            font-size: 50px;
            width: 100%;
            display: flex;
            justify-content: center;
          }

          .laufende-vertrage-text {
            font-size: 14px;
          }
        }
      }
    }
  }
}

//
//IF VERTRAGSDETAIL FENSTER IS OPEN
//
.rowCenter1DetailIsOpen {
  width: 50% !important;
}

.twoRowCenterDetailIsOpen {
  height: 142px !important;
  width: 50% !important;
  flex-direction: column !important;
  left: 50% !important;

  .rowCenter2DetailIsOpen {
    width: 100% !important;
    height: 50% !important;

    .rowCenterContent2DetailIsOpen {
      margin: 0 !important;
      height: 68px !important;

      .gesamtkostenDetailIsOpen {
        font-size: 42px !important;

        .gesamtkostenAfterDetailIsOpen {
          font-size: 16px !important;
          margin-top: 8px !important;
        }
      }
    }
  }

  .rowCenter3DetailIsOpen {
    width: 100% !important;
    height: 50% !important;

    .rowCenterContent3DetailIsOpen {
      margin: 0 !important;
      height: 68px !important;
      border-top: $border !important;

      .laufendeVertrageDetailIsOpen {
        font-size: 42px !important;
      }
    }
  }
}

@media screen and (max-width: 1190px) {
  .row-center-1 {
    width: 50% !important;
    transition-duration: 0.5s;
  }

  .row-center-2,
  .row-center-3 {
    height: 50% !important;
    width: 100% !important;
    transition-duration: 0.5s;
  }

  .two-row-center {
    flex-direction: column !important;
    width: 50% !important;
    left: 50% !important;
    height: 142px !important;
    transition-duration: 0.5s;
  }

  .row-center-content-2,
  .row-center-content-3 {
    width: 70%;
    height: 68px !important;
    margin: auto !important;
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    border-bottom-left-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
  }


  .gesamtkosten,
  .laufende-vertrage {
    font-size: 42px !important;
    transition-duration: 0.5s;
  }

  .gesamtkostenAfter {
    font-size: 16px !important;
    margin-top: 8px !important;
    transition-duration: 0.5s;
  }
}

@media screen and (max-width: 1000px) {
  .row-info-window {
    margin: 30px 0px 30px 0px !important;
    height: 290px;
  }

  .row-center {
    height: 280px !important;
    width: 100% !important;
    border-style: none !important;
    display: flex !important;
    flex-direction: column !important;
  }


  .row-center-2,
  .row-center-3,
  .row-center-1,
  .two-row-center {
    position: relative !important;
    width: 100% !important;
  }

  .row-center-2,
  .row-center-3 {
    height: 75px !important;
    left: 0px !important;
    border-style: hidden !important;
  }

  .two-row-center {
    left: 0% !important;
    height: 150px !important;
  }

  .row-center-content-1 {
    justify-content: center !important;
  }

  .avatar-container {
    margin-right: 5px !important;
  }

  .avatar-info-container {
    margin-left: 5px !important;
  }

  .row-center-content-2 {
    border-top: $borderLila !important;
    border-top-left-radius: 0px !important;
    border-top-right-radius: 0px !important;
  }

  .row-center-content-2,
  .row-center-content-3 {
    flex-direction: row !important;
    position: absolute !important;
    width: 100% !important;
    height: 75px !important;
  }

  .gesamtkosten-text,
  .laufende-vertrage-text {
    text-align: left;
    margin: 30px 0 0 5px !important;
    width: 58% !important;
  }

  .gesamtkosten,
  .laufende-vertrage {
    justify-content: flex-end !important;
    margin-right: 5px !important;
    width: 42% !important;
    font-size: 55px !important;
    margin: 5px 5px 10px 0 !important;
  }
}

@media screen and (max-width: 700px) {
  .row-info-window {
    margin-top: 0px !important;

    .row-center {
      flex-direction: column-reverse !important;
      height: 340px !important;

      .row-center-1 {
        border: none !important;
        position: absolute !important;
        top: 120px !important;


        .row-center-content-1 {
          flex-direction: column !important;
          height: auto !important;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 0px !important;

          .avatar-container {
            margin: 0px !important;

          }

          .avatar-info-container {
            text-align: center !important;
            margin: 0px !important;
            font-size: 14px;
            width: 80% !important;
            color: black;

            p {
              color: $lila !important;
              font-weight: bold;
              margin-bottom: 0px !important;
            }

            #kontakt-aufnehmen-button {
              position: static;
              display: flex;
              margin: auto;
              margin-top: 20px !important;
              width: 60%;
              background-color: $gruen;
              border-radius: 15px;

              &:hover {
                background-color: darken($color: $gruen, $amount: 10);
              }

              p {
                color: #ffffff !important;
                display: flex;
                margin: auto;
              }
            }
          }
        }
      }

      .after {
        height: 15px !important;
        width: 100% !important;
        background-image: #7262f4 !important;
        position: absolute !important;
        top: 100px !important;

        .inside-after {
          width: 100%;
          height: 100%;
          border-top-left-radius: 15px;
          background-color: #ffffff;
        }
      }
    }

    .two-row-center {
      flex-direction: row !important;
      height: 100px !important;
      width: 100% !important;
      background-image: linear-gradient(to left, $lila, rgba(68, 49, 243, 0.76)) !important;
      color: #ffffff;
      border: none !important;
      border-bottom-right-radius: 15px !important;
      position: absolute !important;
      top: 0px !important;

      .row-center-2,
      .row-center-3 {
        border: none !important;
        height: 100px !important;

        .row-center-content-2,
        .row-center-content-3 {
          flex-direction: column !important;
          border: none !important;
          justify-content: center !important;
          align-items: center !important;
          height: 100px !important;

          .gesamtkosten,
          .laufende-vertrage {
            font-size: 30px !important;
            margin: 0px !important;
            justify-content: center !important;
            font-weight: bold;

            .gesamtkostenAfter {
              font-size: 30px !important;
              margin: 0px !important;
              margin-left: 5px !important;
            }
          }

          .gesamtkosten-text,
          .laufende-vertrage-text {
            margin: 0px !important;
            text-align: center !important;
            font-style: italic;
          }
        }
      }
    }
  }

  .container {
    margin-top: 50px !important;

    .infoAlert {
      background-color: lighten($color: $lila, $amount: 45) !important;

      .MuiAlert-icon {
        display: flex;
        margin: auto;

        .MuiSvgIcon-root {
          margin: 8px;
        }
      }

      .MuiAlert-message {
        .MuiTypography-root {
          color: $lila !important;
        }

        p {
          margin: 0px;
          font-size: 13px;
        }
      }
    }
  }
}
