// VARIABLES //
@import "../../mainSCSS/_variables";
// MIXINS //
@import "../../mainSCSS/_mixins";

#right-container-info {
  position: fixed;
  top: 0px;
  right: 0px;
  max-width: 400px;
  height: 100%;
  background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
  background-color: white;
  z-index: 102;
  border-left: $border;
  overflow-y: auto;

  .right-container-info-top {
    color: $textColorBlack;

    .right-container-info-header {
      width: 100%;
      height: 75px;
      background: $lilaBackgroundLight;

      #info-header-close {
        color: $lila;
        position: absolute;
        right: 40px;
        top: 15px;
        cursor: pointer;
        font-size: 110%;
        z-index: 10;
      }
    }

    .right-container-info-header-avatar {
      background-image: url(../../assets/images/avatar.jpg);
      height: 100px;
      width: 100px;
      border-radius: 50%;
      position: relative;
      margin: auto;
      bottom: 50px;
      border: 2px solid $lila;
    }

    .right-container-info-header-avatar-text {
      text-align: center;
      position: relative;
      bottom: 40px;
      font-size: 16px;
      color: $lila;
    }
  }
}

#right-container-info-erledigt {
  @include flexColumn;
  margin: 20px;
  text-align: center;

  h1 {
    color: $lila;
  }

  p{
    color: black;
  }

  .info-handy-button {
    @include button;
    font-weight: bold;
    color: $lila;
    border: 2px solid $lila;

    &:hover {
      background: $lila;
      color: white;
      border: 2px solid white;
    }
  }
}

#right-container-info-handy {
  @include flexColumn;
  text-align: center;
  margin: 20px;
  font-size: 16px;

  a {
    color: $lila;
  }

  p {
    color: black;
  }

  .info-handy-button {
    @include button;
    font-weight: bold;
    color: $lila;
    border: 2px solid $lila;

    &:hover {
      background: $lila;
      color: white;
      border: 2px solid white;
    }
  }
}

#right-container-info-form {
  color: $textColorGrey;
  margin: 20px;
  font-size: 16px;

  .right-container-info-form-text {
    margin-bottom: 15px;
    color: black;

    p {
      font-family: $mainFont;
      font-size: 24px;
      color: $lila;
      font-weight: bold;
    }
  }

  .right-container-info-form-fields {
    @include formFields;

    label {
      margin-bottom: 10px;
      color: $lila;
      font-weight: bold;
    }
  }

  #right-container-info-form-fields2 {
    @include formFields;

    label {
      margin-bottom: 10px;
      color: $lila;
      font-weight: bold;
    }
  }

  #right-container-info-form-fields3 {
    margin-top: 20px;

    flex-direction: column;

    label {
      font-size: 16px;
      color: black;
    }

    textarea {
      width: 100%;
      margin: 10px 0 20px 0;
      font-size: 16px;
      border: $border;
    }

    #right-container-button-submit {
      @include button;
      margin-top: 30px;
      font-size: 20px;
      color: white;
      background-color: white;
      width: 250px;
      text-align: center;
      color: $lila;
      border: 2px solid $lila;

      &:hover {
        background: $lila;
        color: white;
        border: 2px solid white;
      }
    }
  }
}
@include MaxMediaQ(700px) {
  #right-container-info {
      bottom: 70px;
      height: auto;
  }
}
