@import "../../../../mainSCSS/mixins";
@import "../../../../mainSCSS/variables";

.raised-button-file {
  border: 1px solid $lila;
  width: 50px;
  text-align: center;
  border-radius: 15px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.uploadFileElement {
  color: black;
  border: 1px solid $lila;
  border-radius: 10px;
  margin-top: 10px;
  height: 50px;
  display: flex;
  flex-direction: row;

  .fileIcon {
    font-size: 30px;
    margin: 10px 30px;
    color: $lila;
  }

  .trashIcon {
    position: absolute;
    right: 0px;
    color: rgba(255, 0, 0, 0.719);
    padding: 20px 35px 20px 0px;
    cursor: pointer;
    font-size: 16px;
    max-width: 200px;
  }

  p {
    font-size: 12px;
    margin: auto 10px;
    width: 60%;
  }
}

.MuiIconButton-label {
  color: $lila;
}
