@import "../../mainSCSS/mixins";
@import "../../mainSCSS/variables";

.gesContainer {
  width: auto;
  min-width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: auto;
  top: 0;
  bottom: 0;
  position: absolute;
  background-color: white;
  .searchContainer {
    margin: 10px;
    display: flex;
    flex-direction: column;
    .buttonAdmin2 {
      text-transform: none;
    }
  }
  .adminContainer {
    background-color: white;
    overflow-y: auto;
    list-style: none;
  }
}
.MuiTableRow-root {
  cursor: pointer;
  .MuiTableCell-root {
    padding: 5px;
    border-right: $border;
    p {
      margin: 0px;
      float: right;
      font-weight: bold;
    }
    label {
      border: 1px solid rgb(124, 195, 18);
      border-radius: 5px;
      color: rgb(124, 195, 18);
      text-align: center;
      width: 130px;
      margin: 0px auto 20px auto;
      display: flex;
      align-content: center;
      justify-content: center;
      cursor: pointer;
      height: 35px;
      padding: 6px;
      box-sizing: border-box;
    }
  }
  &:hover {
    background-color: aliceblue;
  }
  .buttonAdmin {
    display: flex;
    margin: auto;
    text-transform: none;
    width: 130px;
  }
}
