// VARIABLES //
@import "../../mainSCSS/_variables";
// MIXINS //
@import "../../mainSCSS/_mixins";

.mainVU {
    @include main;
    right: 0px;
    height: auto;
    .logo1 {
        width: 300px;
        height: 300px;
        filter: brightness(0) invert(1) opacity(10%);
        position: absolute;
        right: -150px;
        top: 500px;
        background-size: cover;
        transform: rotate(90deg);
        z-index: 2000000;
    }

    .logo2 {
        width: 300px;
        height: 300px;
        filter: brightness(0) invert(1) opacity(10%);
        position: absolute;
        left: -150px;
        top: 60px;
        background-size: cover;
        transform: rotate(90deg);
        z-index: 2000000;
    }
}

@include MaxMediaQ(700px) {
    #mainVU {
        left: 0px;
        bottom: 60px;

        .logo1 {
            right: -180px;
        }

        .logo2 {
            left: -180px;
        }
    }
}

//
//styles wenn VertragsDetails is opened
//
.mainVUDetailIsOpen {
    @include main;
    right: 30%;
    height: auto;
}
