@import "../../../mainSCSS/mixins";
@import "../../../mainSCSS/variables";

#panel-ausfullen {
  width: 100%;
  height: 100%;
  top: 0px;
  position: absolute;
  z-index: 1000;
  overflow-y: auto;
  background-color: $lilaOpacityBackground;
  padding: 70px 0 70px 0;
  box-sizing: border-box;

  #panel-ausfullen-details {
    display: flex;
    background: #ffffff;
    max-width: 500px;
    position: relative;
    margin: auto;
    border-radius: 25px;

    #panel-ausfullen-details-main {
      width: 100%;
      border-radius: 15px;

      .panel-ausfullen-head {
        height: 180px;
        border-bottom: 1px solid lighten($color: $lila, $amount: 30);
        color: #ffffff;
        padding: 10px;
        box-sizing: border-box;
        display: flex;
        flex-direction: row-reverse;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        background-color: lighten($color: $lila, $amount: 40);


        a {
          position: absolute;
          right: 20px;
          cursor: pointer;
          color: $lila;

          &:hover {
            font-size: 20px;
          }
        }

        .header-container {
          display: flex;
          flex-direction: row-reverse;
          margin: auto;

          .text-container {
            display: flex;
            flex-direction: column;

            p {
              font-size: 20px;
              margin-left: 20px;
            }

            p:nth-child(2n +1) {
              color: $lila;
              font-weight: 800;
              margin-bottom: 0px;
            }

            p:nth-child(2n+2) {
              font-weight: 600;
              color: black;
              margin-top: 10px;
            }
          }

          .image-container {
            display: flex;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            background-color: white;

            img {
              display: flex;
              margin: auto;
              max-width: 70px;
              max-height: 70px;
              background-color: white;
            }
          }
        }
      }
    }
  }
}

.panel-ausfullen-form {
  display: flex;
  flex-direction: column;

  .MuiFormControlLabel-root {
    margin: 0px 0px 26px 0px;
    flex-direction: row;
  }

  .text {
    color: $lila;
    font-weight: 600;
    font-size: 20px;
    height: 36px;
    padding: 10px 0 10px 10px;
    box-sizing: border-box;
    margin-bottom: 20px;
  }

  .MuiTextField-root,
  .MuiFormControl-root {
    .MuiInputBase-root {
      height: 50px;
      background-color: $lilaBackgroundHell;

      input:-webkit-autofill,
      input:-webkit-autofill:hover,
      input:-webkit-autofill:focus,
      input:-webkit-autofill:active {
        -webkit-box-shadow: 0 0 0 30px $lilaBackgroundHell inset !important;
      }

      .MuiOutlinedInput-input {
        padding: 15px 14px;
      }
    }
    .MuiFormLabel-root{
        color: black !important;
    }
    .MuiOutlinedInput-multiline{
      height: 150px!important;
      width: 100%!important;
    }
  }

  .panel-ausfullen-form-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .kuendigung-button{
      background-color: $buttonBlue;
      width: 90%!important;
      &:hover{
        background-color: darken($color: $buttonBlue, $amount: 10);
      }
    }
    .MuiButtonBase-root{
      height: 50px;
      width: 40%;
      text-transform: none;
      border-radius: 25px;
      span{
        a{
          width: 100%;
          height: 50px;
          color: white;
          align-items: center;
          justify-content: center;
          display: flex;
        }
      }
    }
    .MuiTextField-root,
    .MuiFormControl-root {
      .MuiInputBase-root {
        height: 50px;
        background-color: $lilaBackgroundHell !important;

        input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
          -webkit-box-shadow: 0 0 0 30px $lilaBackgroundHell inset !important;
        }

        .MuiOutlinedInput-input {
          padding: 15px 14px;
        }
      }
    }

    .felder-date {
      width: 100% !important;

      input[type="date"]::-webkit-clear-button,
      input[type="date"]::-webkit-inner-spin-button {
        display: none;
      }

      input[type="date"]::-webkit-calendar-picker-indicator {
        color: #2c3e50;
      }

      input[type="date"] {
        appearance: none;
        -webkit-appearance: none;
        color: $backgroundGrey;
        font-family: "Helvetica", arial, sans-serif;
        font-size: 18px;
        border: 1px solid #ecf0f1;
        background: #ecf0f1;
        padding: 5px;
        display: inline-block !important;
        visibility: visible !important;
      }

      input[type="date"],
      focus {
        color: #95a5a6;
        box-shadow: none;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
      }
    }
  }

  .textField-row{
    .MuiFormControl-root{
      width: 100%;
    }
  }
}

.felder {
  width: 46%;
  margin: 2%;

  .button-loschen,
  input,
  button {
    height: 46px;
    width: 98%;
    font-size: 18px;
    box-sizing: border-box;
    padding-left: 3%;
  }

  .felder-button {
    color: #ffffff;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
  }

  .button-speichern {
    background: $textColorBlue;
    border: 2px solid #6f92fc;
    text-transform: none;

    &:hover {
      background: lighten(#4169e1, 20%);
    }
  }

  .button-loschen {
    background: #f97860;
    border: 2px solid #fa755e;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: none;

    &:hover {
      background: lighten(#f97860, 5%);
    }
  }

  .error {
    color: red;
    font-size: 13px;
    width: 100% !important;
  }
}

.felder-textarea {
  width: 100% !important;

  textarea {
    width: 99%;
    font-size: 16px;
  }
}

.felder-kosten {
  width: 100% !important;

  input {
    width: 46% !important;
    margin-bottom: 5px;
  }

  .price-error {
    color: red;
    font-size: 13px;
    width: 100% !important;
  }
}

.felder-checkbox {
  margin: 2% 2% 0 2%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;

  input {
    margin: 10px;
    height: 20px;
    width: 20px;
  }
}

.MuiButton-containedPrimary {
  background-color: rgb(6, 180, 6) !important;

  &:hover {
    background-color: green !important;
  }
}

.MuiTypography-body1 {
  color: black;
}

@include MaxMediaQ(700px) {
  #panel-ausfullen {
    bottom: 70px !important;
    height: auto !important;
  }
}
