// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";

.auth-form {
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: auto;

  input {
    margin-bottom: 20px;
  }
}

.alert-info {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: auto;
  color: #f44336;
  margin-bottom: 20px;
  text-align: center;
  font-size: 14px;
}

.alert-info2 {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: auto;
  color: $lila;
  margin: 20px 0px;
  text-align: center;
  font-size: 14px;
  border: 1px solid $lila;
  /* background-color: $lila; */
  padding: 10px 0px;
  border-radius: 10px;
}
.alert-info3 {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin: auto;
  color: $gruen;
  text-align: center;
  font-size: 14px;
  padding: 0px 0px 20px 0px;
  border-radius: 10px;
}

form {

  .MuiTextField-root,
  .MuiFormControl-root {
    .MuiInputBase-root {
      height: 50px;
      .MuiOutlinedInput-input {
        padding: 15px 14px;
      }
    }
    .MuiFormLabel-root {
      font-size: 13px;
    }
  }
}
