// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";

.vertrag-container {
  display: flex;
  flex-direction: column;
  margin: 50px 20px 50px 20px;

  .alert-box-info {
    display: flex;
    justify-content: center;

    .alert-info3 {
      padding: 0 0 20px 10px;
      color: $textColorGrey;
      font-size: 20px;
      border-bottom: $border;
      width: 1100px;
      cursor: pointer;
    }
  }
}

.VertraegsListContainer {
  padding: 0px 20px;

  .vertrag-main1 {
    display: flex;
    flex-direction: column;
    justify-content: center;

    .container-for-rowlist {
      display: flex;
      justify-content: center;

      .row-list {
        width: 1100px;
        height: 100px;
        display: flex;
        border: 1px solid rgba(255, 255, 255, 0.794);
        border-bottom-left-radius: 50px;
        border-top-left-radius: 50px;
        border-bottom-right-radius: 50px;
        border-top-right-radius: 50px;
        color: black;
        margin-bottom: 5px;
        cursor: pointer;
        background-image: white;
        border-bottom: 2px solid transparent !important;
        border-left-width: 0 !important;
        border-top-width: 0 !important;
        border-right-width: 0 !important;
        border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
        border-image-slice: 1 !important;

        &:hover {
          background: $lilaBackgroundLight;
        }

        #image-container {
          min-width: 90px;
          width: 90px;
          height: 90px;
          cursor: pointer;
          display: flex;
          justify-content: center;
          border: 1px solid lighten($color: $lila, $amount: 30);
          border-radius: 50%;
          padding: 5px;
          box-sizing: border-box;
          margin: 5px;
          background-color: white;
          position: relative;

          .vertragLogo {
            display: none;
            position: absolute;
            height: 25px;
            width: 25px;
            top: -3px;
            right: -10px;
            /* display: flex; */
            justify-content: center;
            align-content: center;

            .vertragLogoImage {
              max-width: 25px;
              max-height: 25px;
            }
          }

          .image {
            margin: auto;
            height: 50px;
            font-size: 8px;
            text-decoration: none;
          }
        }



        .rest-info-container {
          width: 100%;
          box-sizing: border-box;
          margin: 5px;
          display: flex;
          flex-direction: column;
          font-size: 14px;
          justify-content: space-around;

          p:nth-child(2n+1) {
            font-weight: 800;
          }

          .image {
            height: 50px;
            display: flex;
            background-repeat: no-repeat;
            margin: auto;
            margin-left: 50px;
            background-position-x: center;
            background-position-y: center;
          }

          .container {
            flex-direction: row;
            display: flex;
            height: 80%;
            margin: 0px;

            div {
              margin-right: 10px;
            }

            p {
              font-size: 15px;
              margin-left: 0px !important;
              margin-right: 0px !important;
            }

            .image1 {
              height: 50px;
              display: flex;
              background-repeat: no-repeat;
              margin: 10px auto 0px auto;
              margin-left: 50px;
              background-position-x: center;
              background-position-y: center;
            }
          }
        }
      }
    }
  }
}

.vertragsList {
  padding-top: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;

  .fade {
    border-bottom: $border;

    button {
      margin-right: 20px;
    }
  }
}

#progressgreen,
#progressyellow,
#progressred {
  position: relative;
  top: 5px;
  width: 95%;
  max-height: 5px;
  -webkit-appearance: none;
  appearance: none;
}

#progressred[value]::-webkit-progress-bar,
#progressyellow[value]::-webkit-progress-bar,
#progressgreen[value]::-webkit-progress-bar {
  background-color: #eee;
  border-radius: 2px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
}

#progressred[value]::-webkit-progress-value {
  background: rgba(241, 77, 48, 0.5);
}

#progressyellow[value]::-webkit-progress-value {
  background: hsla(66, 83%, 66%, 0.5);
}

#progressgreen[value]::-webkit-progress-value {
  background: hsla(138, 81%, 64%, 0.5);
}

.MuiTabs-root {
  background: white;
  border: 2px solid lighten($color: $lila, $amount: 0);
  box-shadow: 3px 3px 3px 1px lighten($color: $lila, $amount: 30) !important;
  z-index: 20000000;

  .MuiTabs-scroller {
    .PrivateTabIndicator-root-94 {
      height: 4px;
    }

    .MuiTabs-flexContainer {
      button {
        border-right: 2px solid $lila;
      }
    }
  }
}

.MuiAppBar-root {
  width: auto !important;
}

.MuiTabs-indicator {
  height: 48px !important;
  background-color: $lilaBackgroundLight !important;
  box-shadow: 0px -1px 5px 0px $lilaBackgroundLight !important;
}

.MuiTab-fullWidth {
  color: $lila !important;
  font-size: 17px !important;
}

.MuiTab-wrapper {
  text-transform: none !important;
  font-weight: bold;
}

.MuiAppBar-colorDefault {
  background-color: #ffffff !important;
}

.MuiPaper-elevation0 {
  .MuiAlert-message {
    color: black !important;
  }
}

.react-swipeable-view-container {
  background: white !important;
  opacity: 1 !important;
}

//VertragsList
@include MaxMediaQ(1000px) {
  .MuiTabs-flexContainer {
    flex-direction: column;
  }

  .MuiTabs-indicator {
    height: 0px !important;
    background-color: none;
    box-shadow: none;
  }

  .MuiTabs-root {
    border-right: none;
    border-left: none;

    .MuiTabs-scroller {
      .PrivateTabIndicator-root-94 {
        height: 0px !important;
      }
    }
  }

  .row-list {
    border-bottom-left-radius: 0px !important;
    border-top-left-radius: 0px !important;
    border-bottom-right-radius: 0px !important;
    border-top-right-radius: 0px !important;
    border-left-style: none !important;
    border-right-style: none !important;
  }

  .VertraegsListContainer {
    padding: 0px;
  }

  .MuiTab-fullWidth {
    border-top: 2px solid transparent !important;
    border-left-width: 0 !important;
    border-bottom-width: 0 !important;
    border-right-width: 0 !important;
    border-image: linear-gradient(to right, #f8f8f8 0%, rgba($color: #ff8de369, $alpha: .8) 50%, #f8f8f8 100%) !important;
    border-image-slice: 1 !important;
    padding: 0 0 0 0 !important;
  }

  .MuiBox-root {
    padding: 24px 0px !important;
  }

  .makeStyles-root {
    padding: 0px !important;
  }

  .MuiPaper-root {
    width: auto !important;
    margin: 0px !important;
  }
}

@include MaxMediaQ(700px) {
  .row-list {
    background: $lilaBackgroundHell !important;
    height: 80px !important;

    &:hover {
      background: darken($color: $lilaBackgroundHell, $amount: 5) !important;
    }

    #image-container {
      height: 70px !important;
      width: 70px !important;
      min-width: 70px !important;
      position: relative;

      .vertragLogo {
        position: absolute;
        height: 20px;
        width: 20px;
        top: -3px;
        right: -10px;
        display: flex !important;
        justify-content: center;
        align-content: center;

        .vertragLogoImage {
          max-width: 20px !important;
          max-height: 20px !important;
        }
      }

      .image {
        margin-top: 5px !important;
        max-width: 50px;
        max-height: 50px;
        position: absolute;
      }
    }

    .rest-info-container {
      justify-content: space-around;

      p {
        display: flex;
        margin-left: 10px !important;
        margin-right: 0px !important;
      }

      .container {
        margin: 0px !important;
        padding-top: 0px !important;
        margin-left: 10px !important;
        flex-direction: column !important;
        max-width: 100% !important;
        width: auto !important;
      }
    }
  }
}

@include MaxMediaQ(350px) {
  .row-list {
    width: 100% !important;
  }

  #progressgreen,
  #progressyellow,
  #progressred {
    width: 90% !important;
  }
}
