@import "../../mainSCSS/mixins";
@import "../../mainSCSS/variables";

.popup4-overlay {
  background-color: $lilaOpacityBackground !important;
  overflow-y: auto;
  padding: 50px 0px !important;
  box-sizing: border-box !important;

  .popup4-content {
    padding: 0px !important;
    max-width: 500px !important;
    border-radius: 25px;

    .panel-ausfullen-form {
      border-radius: 25px;
      display: flex;
      flex-direction: column;
      width: 500px;

      .info-header3 {
        @include infoHeader;
        left: 0px;
        top: -50px;
      }

      .form-header {
        background-color: $lilaBackgroundHell;
        padding: 20px 10px 0px 10px;
        margin-bottom: 20px;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;

        .panel-ausfullen-form-row {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom: 20px;

          #button-close-info-vertrag-uebersicht {
            cursor: pointer;
            border: none;
            background-color: lighten($color: $lila, $amount: 50);
            color: $lila;
            font-size: 18px;
            padding-top: 2px;
            box-sizing: border-box;
            font-weight: bold;

            &:hover {
              font-size: 20px;
              padding-top: 0px;

            }
          }

          #button-close-info-vertrag-uebersicht2 {
            display: none;
          }
        }

        .text {
          background-color: lighten($color: $lila, $amount: 40);
          border-bottom: 2px solid transparent !important;
          border-left-width: 0 !important;
          border-top: 1px solid transparent !important;
          border-right-width: 0 !important;
          border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
          border-image-slice: 1 !important;
          font-size: 16px;
        }

        p {
          text-align: center;
          color: black;
          font-size: 13px;
          margin: 0px 0px 20px 0px;
        }

        .panel-ausfullen-form-row {
          margin-bottom: 0px;
        }
      }

      .after {
        display: none;
      }

      .form-body {
        padding: 10px;

        .panel-ausfullen-form-row {
          margin-bottom: 0px;
        }

        .panel-ausfullen-form-row2 {

          .MuiFormControl-root {
            width: 100%;

          }
        }

        .text {
          background-color: lighten($color: $lila, $amount: 40);
          border-bottom: 2px solid transparent !important;
          border-left-width: 0 !important;
          border-top: 1px solid transparent !important;
          border-right-width: 0 !important;
          border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
          border-image-slice: 1 !important;
          font-size: 16px;
        }
      }
    }
  }
}

.MuiInputBase-input {
  font-size: 20px;
}

@include MaxMediaQ(700px) {
  .popup4-overlay {
    bottom: 70px !important;
    padding: 0px 0px !important;

    .popup4-content {
      border-radius: 0px;

      .panel-ausfullen-form {
        .form-header {
          height: 100px;
          border-top-left-radius: 0px;
          border-top-right-radius: 0px;
          background-image: linear-gradient(to left, $lila, rgba(68, 49, 243, 0.76)) !important;
          border-bottom-right-radius: 25px;


          .panel-ausfullen-form-row {
            height: 100px;
            width: 100%;
            display: flex;

            a {
              #button-close-info-vertrag-uebersicht {
                display: none;
              }

              #button-close-info-vertrag-uebersicht2 {
                display: block !important;
                position: absolute;
                left: 15px;
                top: 15px;
                color: white;
                font-size: 26px;
              }
            }

            p {
              color: white;
              position: static;
              margin: auto;
              font-size: 30px;
              font-weight: bolder;
              letter-spacing: 2px;
            }

          }

          p {
            position: absolute;
            bottom: 100px;
            font-size: 14px;
            margin: 0px 30px;
            color: $lila;

          }
        }

        .after {
          display: block;
          position: absolute;
          width: 100%;
          height: 25px;
          background: #715aee;
          top: 120px;

          .inside-after {
            background: white;
            height: 100%;
            width: 100%;
            border-top-left-radius: 25px;
          }
        }

        .form-body {
          margin-bottom: 60px;
        }

        .form-body,
        .panel-ausfullen-first-content {
          .panel-ausfullen-user-photo {
            width: 120px;
            height: 120px;
            padding: 10px;
            box-sizing: border-box;
            margin: 0px 10px;
            background-color: $lilaBackgroundHell;
            font-size: 50px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $lila;
            font-size: 80px;
          }

          .panel-ausfullen-form-row-700px,
          .panel-ausfullen-form-row2-700px {
            display: flex;
            flex-direction: column;

            .MuiInputLabel-outlined {
              transform: translate(14px, 10px) scale(0.75);
            }

            .MuiInputLabel-outlined.MuiInputLabel-shrink {
              transform: translate(14px, -6px) scale(0.75);
            }

            .MuiInputBase-formControl {
              height: 30px;
              background-color: $lilaBackgroundHell;

              input:-webkit-autofill,
              input:-webkit-autofill:hover,
              input:-webkit-autofill:focus,
              input:-webkit-autofill:active {
                -webkit-box-shadow: 0 0 0 30px $lilaBackgroundHell inset !important;
              }

              .MuiInputBase-input {
                border: none;
              }

              .MuiOutlinedInput-notchedOutline {
                border: none;
              }

              .MuiOutlinedInput-input {
                padding: 0px 14px;
              }
            }
          }
        }
      }
    }
  }
}

@include MaxMediaQ(500px) {
  .popup4-content {
    width: 100% !important;
  }

  .panel-ausfullen-form {
    width: 100% !important;
  }
}
