// VARIABLES //
@import "../../../mainSCSS/_variables";
// MIXINS //
@import "../../../mainSCSS/_mixins";

.titel {
  padding: 20px;
  color: $lila;
  text-decoration: none;
  font-weight: bold;
  height: 75px;
  font-size: 23px;
  display: flex;
  box-sizing: border-box;
  position: relative;
  border-bottom: 3px solid $lilaBackgroundHell;

  div {
    background-color: $gruen !important;
    color: white !important;
    background-color: rgba(255, 255, 255, 0.445) !important;
    color: white !important;
    border: 1px solid white;

    &:hover {
      background-color: white !important;
      color: $greenColor !important;
      background-color: rgba(255, 255, 255, 0.171) !important;
    }
  }

  p {
    margin: 0px;
  }

  .vertrage-hinzufugen-button {
    font-size: 14px;
    text-transform: none;
    border-radius: 5px;
    position: absolute;
    right: 20px;
    top: 15px;
    height: 14px;
    padding: 15px;
    cursor: pointer;
    align-content: center;
  }
}

@include MaxMediaQ(700px) {
  .titel {
    display: none;
  }
}
