// VARIABLES //
@import "../../../../mainSCSS/_variables";
// MIXINS //
@import "../../../../mainSCSS/_mixins";

.container {
  width: 100%;

  display: flex;
  margin: auto;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;

  .infoAlert {
    min-height: 70px;
    max-width: 1100px;
    width: 100%;
    padding: 0px !important;
    border: $border;
    border-radius: 10px;
    background-color: $lilaBackgroundLight !important;

    .infoText {
      color: #2196f3;
      cursor: pointer;
    }

    .MuiAlert-icon {


      .MuiSvgIcon-fontSizeInherit {
        font-size: 50px;
        margin-left: 10px;
        display: flex;
        margin: auto;
      }
    }

    .MuiAlert-message {
      p {
        color: black;
      }
    }
  }
}

.popup2-overlay {
  background-color: $lilaOpacityBackground !important;
  overflow-y: auto;
  overflow-x: hidden !important;

  .popup2-content {
    min-width: 400px;
    width: 400px;
    padding: 0px !important;
    border: none !important;
    overflow-x: hidden !important;
    position: relative !important;
    background-color: rgba(121, 7, 7, 0) !important;


    .containerAssistent {
      display: flex;
      flex-direction: row;
      min-height: 500px;
      overflow-y: hidden;
      overflow-x: hidden;
      position: relative;
      transition-duration: 0.7s;
      background-color: rgba(0, 0, 0, 0);
      padding: 20px 0px;

      .firstCard,
      .lastCard {
        min-height: 1px;
        height: 100%;
        width: 400px;
        display: flex;
        position: relative;
        flex-direction: column;
        background-color: white;
        border-radius: 25px;

        .containerAssistentHeader {
          position: relative;
          width: 400px;
          height: 180px;
          background-color: $lilaBackgroundHell;
          display: flex;
          flex-direction: row;
          border-top-left-radius: 25px;
          border-top-right-radius: 25px;

          #buttonCloseDialog,
          #buttonCloseDialog1 {
            color: $lila;
            position: absolute;
            right: 15px;
            top: 15px;

            &:hover {
              font-size: 20px;
              right: 14px;
              top: 14px;
            }
          }

          .headerPhoto {
            height: 140px;
            width: 140px;
            border-radius: 50%;
            border: 1px solid $lila;
            margin-top: 30px;
            background-image: url(../../../../assets/images/avatar.jpg);
            background-size: contain;
            margin: auto 20px;
          }

          .headerTextContainer {
            margin-bottom: 20px;
            display: flex;
            justify-content: center;
            align-content: center;
            flex-direction: column;

            p {
              text-align: left;
              margin: 0px;
              color: $lila;
              font-weight: bold;
            }

            i {
              color: black;
            }
          }
        }

        .containerAssistentContent {
          width: 400px;
          height: 440px;
          box-sizing: border-box;
          padding: 20px 40px;
          text-align: center;

          .title {
            color: $lila;
            font-size: 18px;
            font-weight: 100;
            font-weight: bold;
          }

          p {
            font-size: 15px;
            line-height: 20px;
            margin-bottom: 30px;
            color: black;
          }

          ul>li {
            list-style: none;
            text-align: left;
            margin-bottom: 10px;
            font-size: 15px;
            color: black;

            span {
              margin-right: 20px;
              color: $greenColor;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
}

.MuiPaper-elevation0 {
  background-color: $lilaBackgroundHell !important;
}

@media screen and (max-width: 1000px) {
  .container {
    padding: 20px 0px;

    .infoAlert {
      width: 100% !important;
    }
  }
}

@media screen and (min-width: 800px) {
  .popup2-content {
    width: 400px !important;
  }
}

@media screen and (max-width: 700px) {
  .container {
    padding: 20px 0px 0px 0px;
  }

  .popup2-overlay {
    bottom: 70px !important;
  }

  .MuiAlert-message {
    p {
      color: $lila !important;
    }
  }
}

@media screen and (max-width: 400px) {

  .firstCard,
  .lastCard,
  .containerAssistentContainer,
  .containerAssistentHeader,
  .containerAssistentContent,
  .cardAssistentContainer {
    width: 320px !important;
  }

  .containerAssistentContent {
    padding: 20px !important;
  }

  .popup-content,
  .popup2-content {
    width: 320px !important;
    min-width: 320px !important;
  }
}
