// VARIABLES //
@import "../../mainSCSS/_variables";
// MIXINS //
@import "../../mainSCSS/_mixins";

#mainVS {
    @include main;
    right: 0px;
    height: auto;

    .donutsContainer {
        display: flex;
        max-width: 1100px;
        margin: 50px auto;
        justify-content: space-between;


        .donutsContainerFirstBox,
        .donutsContainerSecondBox {
            position: relative;
            width: 45%;
            background-color: white;
            background-image: linear-gradient(to left, $lilaBackgroundLight, $lilaBackgroundLight);
            border-radius: 25px;
            display: flex;
            flex-direction: row;
            margin: auto;
            height: 250px;

            .donutChart{
                width: 200px;
            }
            .image {
                height: 100px;
                width: 100px;
                background: white;
                border-radius: 50%;
                z-index: 2;
                position: absolute;
                left: 50px;
                top: 75px;
            }

            .donutsContainerFirstBoxContent {
                width: 50%;

                p {
                    display: flex;
                    padding: 10px 0px 0px 0px;
                    box-sizing: border-box;
                    font-weight: bold;
                    margin: 0px;
                    border-bottom: 2px solid;
                    width: 100%;
                    justify-content: center;

                }

                ul {
                    li {
                        font-size: 25px;
                        position: relative;

                        span {
                            margin-top: 2px;
                            top: 7px;
                            position: absolute;
                            color: $textColorGrey;
                            font-size: 13px;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1190px) {
    .donutsContainer {
        flex-direction: column;

        .donutsContainerFirstBox {
            margin-bottom: 20px !important;
        }

        .donutsContainerFirstBox,
        .donutsContainerSecondBox {
            width: 480px !important;
        }
    }
}

@media screen and (max-width: 800px) {

    .slick-arrow {
        display: none !important;
    }

}

@include MaxMediaQ(700px) {
    #mainVS {
        left: 0px;
        bottom: 60px;
    }

    .donutsContainerFirstBox {
        margin-top: 20px !important;
    }
}

@include MaxMediaQ(480px) {

    #donutsContainerFirstBox,
    #donutsContainerSecondBox {
        width: 100% !important;
    }
}

@include MaxMediaQ(420px) {

    #donutsContainerFirstBox {
        justify-content: center !important;
        height: 200px !important;
        align-items: center !important;

        .donutChart{
            width: 150px;
            min-width: 150px;
        }
        .image {
            height:85px!important;
            width: 85px!important;
            top: 56px !important;
            left: 33px !important;
        }

        .donutsContainerFirstBoxContent {
            width: 80% !important;
        }
    }
    #donutsContainerSecondBox{
        min-height: 250px !important;
        height: auto !important;
        .slick-slider{
            position: relative !important;
        }
        .sliderContener{
           .container{
               flex-direction: row !important;
                .contentSlider{
                    height: auto !important;
                    margin: 25px 0px !important;
                }
                .ring{
                    margin: 25px 15px 25px 15px !important;
                    height: 140px !important;
                    width: 140px !important;
                }
           }
        }

        p{
            padding: 30px 0px 0px 0px;
        }
    }
}
