@import "../../../mainSCSS/mixins";
@import "../../../mainSCSS/variables";


.popup3-overlay {
  background-color: $lilaOpacityBackground !important;
  overflow-y: auto;
  padding: 70px 0px;

  .popup3-content {

    max-width: 500px !important;
    border: none !important;
    padding: 0px !important;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;

    #info-vertrag-uebersicht {
      box-sizing: border-box;
      top: 0;
      background: white;
      cursor: auto;
      height: auto;
      width: 100%;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      .vertrag-uebersicht-content {
        height: auto;

        .after1 {
          display: none;
        }

        .vertrag-uebersicht-head {
          padding: 10px;
          box-sizing: border-box;
          position: relative;
          display: flex;
          flex-direction: row;
          top: 0px;
          max-width: 100%;
          height: 180px;
          background-color: lighten($color: $lila, $amount: 40);
          border-top-left-radius: 25px;
          border-top-right-radius: 25px;

          .vertragLogo {
            min-height: 120px;
            min-width: 120px;
            position: relative;
            border-radius: 60px;
            background-color: white;
            display: flex;
            margin: auto 20px;

            .image {
              max-height: 80px;
              max-width: 80px;
              margin: auto;
            }
          }

          .vertrag-head-text {
            display: flex;
            flex-direction: column;
            margin: auto 0px;
          }

          #button-close-info-vertrag-uebersicht,
          #button-close-info-vertrag-uebersicht2 {
            color: $lila;
            cursor: pointer;
            font-size: 18px;
            width: 35px;
            height: 35px;
            text-align: center;
            position: absolute;
            top: 15px;
            right: 15px;

            &:hover {
              font-size: 20px;
              right: 14px;
              top: 14px;
            }
          }

          #button-close-info-vertrag-uebersicht2 {
            display: none;
          }

          .vertrag-uebersicht-titel {
            font-size: 20px;
            color: $lila;
            box-sizing: border-box;
            font-weight: 800;
          }

          .vertrag-uebersicht-info {
            width: 100%;
            color: black;
            top: 0px;

            p {
              display: flex;
              justify-content: center;
              box-sizing: border-box;
            }
          }
        }

        .vertrag-ubersicht-body {
          position: relative;
          border-bottom-left-radius: 25px;
          border-bottom-right-radius: 25px;

          .button-section {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            border-bottom-left-radius: 25px;
            border-bottom-right-radius: 25px;
            margin: 25px auto;
            .MuiButton-containedPrimary{
              background-color: $buttonBlue!important;
              &:hover {
                background-color: darken($color: $buttonBlue, $amount: 10)!important;
              }
            }
            a {
              color: white;
              position: absolute;
              width: 100%;
              height: 100%;
              display: flex;
              justify-content: center;
              align-items: center;


              .vertrag-uebersicht-kuendigungspanel {
                width: 50%;
                color: $textColorBlue;
                text-align: center;
                border-radius: 5px;
                right: 0px;
                border: 1px solid $textColorBlue;
                cursor: pointer;

                &:hover {
                  transition-duration: 0.5s;
                  -moz-box-shadow: 0px 0px 1px 2px $textColorBlue;
                  -webkit-box-shadow: 0px 0px 1px 2px $textColorBlue;
                  box-shadow: 0px 0px 1px 2px $textColorBlue;
                  background-color: $textColorBlue;
                  color: white;
                }

                p {
                  box-sizing: border-box;
                }
              }
            }

            .vertrag-uebersicht-loschenspanel {
              width: 50%;
              background-color: whitesmoke;
              color: $textColorBlue;
              text-align: center;
              margin: 30px auto 20px auto;
              height: 50px;
              border-radius: 5px;
              right: 0px;
              color: #f97860;
              border: 1px solid #f97860;
              cursor: pointer;

              &:hover {
                transition-duration: 0.5s;
                -moz-box-shadow: 0px 0px 1px 2px #f97860;
                -webkit-box-shadow: 0px 0px 1px 2px #f97860;
                box-shadow: 0px 0px 1px 2px #f97860;
                background-color: #f97860;
                color: white;
              }

              p {
                box-sizing: border-box;
              }
            }

            .vertrag-uebersicht-bereit-gekuendigt {
              text-align: center;
              margin-top: 40px;
            }
          }
        }
      }

    }
  }
}

#vertrag-uebersicht-table {

  padding: 10px 15px 50px 15px;
  width: 100%;
  box-sizing: border-box;

  thead {
    th {
      border-bottom: $border;
    }
  }

  tbody {
    border-top: 2px solid black;

    tr {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      border-bottom: $border;


      th {
        text-align: left;
        padding: 5px 0px 2px 20px;
        font-weight: 100;
        width: 100%;
        box-sizing: border-box;
      }

      td {
        text-align: left;
        padding: 15px 0px 15px 0px;
        font-weight: 100;
        width: 100%;
        box-sizing: border-box;
        font-weight: 600;
        border-bottom: 1.5px solid transparent !important;
        border-left-width: 0 !important;
        border-top-width: 0 !important;
        border-right-width: 0 !important;
        border-image: linear-gradient(to right, rgba(178, 115, 189, 0.137) 0%, rgba($color: #ff8de369, $alpha: .8) 50%, rgba(178, 115, 189, 0.116) 100%) !important;
        border-image-slice: 1 !important;

      }
    }
  }
}

@include MaxMediaQ(1250px) {
  #info-vertrag-uebersicht {
    max-width: 100%;
    width: auto;
    left: 195px;
    right: 0px;
    transition-duration: 0.5s;
  }
}

@include MaxMediaQ(700px) {

  .popup3-overlay {
    padding: 0px !important;
    bottom: 70px !important;

    .popup3-content {
      padding: 0px !important;
      border-bottom-left-radius: 0px !important;
      border-bottom-right-radius: 0px !important;
    }

    .vertrag-uebersicht-head {
      height: 120px !important;
      border-top-left-radius: 0px !important;
      border-top-right-radius: 0px !important;
      background-image: linear-gradient(to left, $lila, rgba(68, 49, 243, 0.76)) !important;
      border-bottom-right-radius: 25px;

      .vertrag-head-text {

        .vertrag-uebersicht-titel,
        .vertrag-uebersicht-info {
          color: white !important;
        }

        .vertrag-uebersicht-titel {
          font-size: 20px !important;
        }

        .vertrag-uebersicht-info {
          font-size: 16px !important;
        }
      }

      .vertragLogo {
        min-width: 80px !important;
        min-height: 80px !important;
        border-radius: 40px !important;
        margin: auto 20px auto 40px !important;

        .image {
          max-height: 60px !important;
          max-width: 60px !important;
        }
      }

      a {
        #button-close-info-vertrag-uebersicht {
          display: none;
        }

        #button-close-info-vertrag-uebersicht2 {
          display: block !important;
          color: white !important;
          left: 10px !important;
        }
      }
    }

    .after1 {
      display: block !important;
      height: 25px;
      width: 100%;
      background-color: #6d51ec;

      .inside-after1 {
        background-color: white;
        border-top-left-radius: 25px;
        width: 100%;
        height: 100%;
      }
    }
  }
}
