@import "../../../mainSCSS/mixins";
@import "../../../mainSCSS/variables";

.searchField-container{
    display: flex;
    justify-content: center;
    .searchInput{
        width: 45%;
        height: 30px;
        margin: 20px auto;
        .MuiFormLabel-root{
            color: black;
        }
        .MuiInputBase-root{
            height: 100%;
            background: #eab8dd;
            .MuiInputAdornment-root{
                .search{
                    color: $lila;
                    display: flex;
                }
            }
        }
        .MuiInputLabel-outlined {
        transform: translate(14px, 8px) scale(1);
        }

        .MuiInputLabel-shrink {
            transform: translate(14px, -6px) scale(0.75);
        }
    }
}
